<template>
	<view class="container">
		<!-- Êä•ÂëäÂ§¥ÈÉ® -->
		<view class="report-header">
			<view class="header-icon">
				<text class="icon-text">üìä</text>
			</view>
			<text class="report-title">ÂèëËÇ≤ËØÑ‰º∞Êä•Âëä</text>
			<text class="report-subtitle">{{ childInfo.name }} ÁöÑÁªºÂêàÂèëËÇ≤ËØÑ‰º∞ÁªìÊûú</text>
			<text class="report-age-range" v-if="ageBand">{{ ageBand.label }} Âπ¥ÈæÑÊÆµËØÑ‰º∞</text>
			<text class="report-mode" v-if="assessmentMode">Ôºà{{ assessmentMode === 'band' ? 'Á≤æÂáÜ' : 'Á¥ØËøõ' }}Ê®°ÂºèÔºâ</text>
		</view>
		
		<!-- ÊÄª‰ΩìËØÑÂàÜ -->
		<view class="score-card">
			<view class="score-circle">
				<text class="score-number">{{ overallCompletionRate }}</text>
				<text class="score-unit">%</text>
			</view>
			<view class="score-info">
				<text class="score-title">{{ getCompletionLevel(overallCompletionRate) }}</text>
				<text class="score-desc">{{ getCompletionDescription(overallCompletionRate) }}</text>
			</view>
		</view>
		
		<!-- ËØ¶ÁªÜÂàÜÊûê -->
		<view class="analysis-section">
			<view class="section-title">
				<text class="title-text">ËØ¶ÁªÜÂàÜÊûê</text>
			</view>
			
			<!-- ÂêÑÈ¢ÜÂüüÂæóÂàÜË°®Ê†º -->
			<view class="score-table">
				<view class="table-header">
					<text class="header-cell">È¢ÜÂüü</text>
					<text class="header-cell">ÂàÜÂ≠ê</text>
					<text class="header-cell">ÂàÜÊØç</text>
					<text class="header-cell">ÊØî‰æã</text>
					<text class="header-cell">Á≠âÁ∫ß</text>
				</view>
				<view 
					class="table-row" 
					v-for="(stats, domain) in domainStats" 
					:key="domain"
				>
					<text class="table-cell domain-name">{{ domain }}</text>
					<text class="table-cell">{{ stats.passed }}</text>
					<text class="table-cell">{{ stats.total }}</text>
					<text class="table-cell">{{ Math.round(stats.ratio * 100) }}%</text>
					<view class="table-cell">
						<view 
							class="level-badge" 
							:class="getLevelClass(stats.level)"
						>
							{{ getLevelText(stats.level) }}
						</view>
					</view>
				</view>
			</view>
			
			<!-- Èõ∑ËææÂõæÂç†‰Ωç -->
			<view class="chart-container" v-if="hasData">
				<text class="chart-title">ÂêÑÈ¢ÜÂüüÂèëËÇ≤Ê∞¥Âπ≥</text>
				<view class="chart-placeholder">
					<text class="chart-text">Èõ∑ËææÂõæÊòæÁ§∫ÂêÑÈ¢ÜÂüüÊØî‰æã</text>
					<view class="chart-bars">
						<view 
							class="chart-bar" 
							v-for="(stats, domain) in domainStats" 
							:key="domain"
						>
							<view 
								class="bar-fill" 
								:style="{ height: stats.ratio * 100 + '%' }"
								:class="getLevelClass(stats.level)"
							></view>
							<text class="bar-label">{{ domain }}</text>
						</view>
					</view>
				</view>
			</view>
			
			<!-- Êó†Êï∞ÊçÆÊèêÁ§∫ -->
			<view class="no-data" v-else>
				<text class="no-data-text">ÊöÇÊó†ËØÑ‰º∞Êï∞ÊçÆ</text>
				<text class="no-data-hint">ËØ∑ÂÖàÂÆåÊàêËØÑ‰º∞</text>
			</view>
		</view>
		
		<!-- ‰∏ì‰∏öÂª∫ËÆÆ -->
		<view class="recommendations-section" v-if="recommendations.length > 0">
			<view class="section-title">
				<text class="title-text">‰∏ì‰∏öÂª∫ËÆÆ</text>
			</view>
			<view class="recommendations-list">
				<view 
					class="recommendation-item" 
					v-for="(rec, index) in recommendations" 
					:key="index"
				>
					<text class="rec-number">{{ index + 1 }}</text>
					<text class="rec-text">{{ rec }}</text>
				</view>
			</view>
		</view>
		
		<!-- Â∫ïÈÉ®Êìç‰Ωú -->
		<view class="action-buttons">
			<button class="action-btn secondary" @click="viewHistory">
				<text class="btn-text">Êü•ÁúãÂéÜÂè≤</text>
			</button>
			<button class="action-btn primary" @click="newAssessment">
				<text class="btn-text">ÈáçÊñ∞ËØÑ‰º∞</text>
			</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				childInfo: {},
				ageBand: null,
				assessmentMode: '',
				isCorrectedAge: false,
				domainStats: {},
				overallCompletionRate: 0,
				recommendations: [],
				hasData: false
			}
		},
		onLoad() {
			this.loadAssessmentResult()
		},
		methods: {
			// Âä†ËΩΩËØÑ‰º∞ÁªìÊûú
			loadAssessmentResult() {
				const result = uni.getStorageSync('assessmentResult')
				console.log('=== Âä†ËΩΩËØÑ‰º∞ÁªìÊûú ===')
				console.log('ËØÑ‰º∞ÁªìÊûú:', result)
				
				if (!result) {
					uni.showToast({
						title: 'Êú™ÊâæÂà∞ËØÑ‰º∞ÁªìÊûú',
						icon: 'none'
					})
					setTimeout(() => {
						uni.navigateBack()
					}, 1500)
					return
				}
				
				this.childInfo = result.childInfo || {}
				this.ageBand = result.ageBand
				this.assessmentMode = result.assessmentMode || 'band'
				this.isCorrectedAge = result.isCorrectedAge || false
				
				// ËÆ°ÁÆóÈ¢ÜÂüüÁªüËÆ°
				this.calculateDomainStats(result)
				
				// ÁîüÊàêÂª∫ËÆÆ
				this.generateRecommendations()
				
				console.log('=== ÁªüËÆ°ÁªìÊûú ===')
				console.log('È¢ÜÂüüÁªüËÆ°:', this.domainStats)
				console.log('ÊÄª‰ΩìÂÆåÊàêÁéá:', this.overallCompletionRate)
			},
			
			// ËÆ°ÁÆóÈ¢ÜÂüüÁªüËÆ°
			calculateDomainStats(result) {
				const stats = {}
				let totalPassed = 0
				let totalQuestions = 0
				
				// ÊåâÈ¢ÜÂüüÁªüËÆ°
				Object.keys(result.groupedQuestions).forEach(domain => {
					const questions = result.groupedQuestions[domain]
					const passed = questions.filter(q => result.questionAnswers[q.id] === true).length
					const total = questions.length
					const ratio = total > 0 ? passed / total : 0
					
					stats[domain] = {
						passed: passed,
						total: total,
						ratio: ratio,
						level: this.getLevelFromRatio(ratio)
					}
					
					totalPassed += passed
					totalQuestions += total
				})
				
				this.domainStats = stats
				this.overallCompletionRate = totalQuestions > 0 ? Math.round((totalPassed / totalQuestions) * 100) : 0
				this.hasData = totalQuestions > 0
				
				console.log('ËÆ°ÁÆóÂÆåÊàê - ÊÄªÈ¢òÁõÆ:', totalQuestions, 'Â∑≤ÂÆåÊàê:', totalPassed, 'ÂÆåÊàêÁéá:', this.overallCompletionRate + '%')
			},
			
			// Ê†πÊçÆÊØî‰æãËé∑ÂèñÁ≠âÁ∫ß
			getLevelFromRatio(ratio) {
				if (ratio >= 1) return 'normal'    // Â∏∏ÊÄÅ (Ëìù)
				if (ratio >= 2/3) return 'good'    // ËâØÂ•Ω (Áªø)
				if (ratio >= 1/2) return 'fair'     // ÊôÆÈÄö (ÈªÑ)
				return 'poor'                      // Â∑Æ (Á∫¢)
			},
			
			// Ëé∑ÂèñÁ≠âÁ∫ßÊ†∑ÂºèÁ±ª
			getLevelClass(level) {
				return {
					'level-normal': level === 'normal',
					'level-good': level === 'good',
					'level-fair': level === 'fair',
					'level-poor': level === 'poor'
				}
			},
			
			// Ëé∑ÂèñÁ≠âÁ∫ßÊñáÊú¨
			getLevelText(level) {
				const levelMap = {
					'normal': 'Â∏∏ÊÄÅ',
					'good': 'ËâØÂ•Ω',
					'fair': 'ÊôÆÈÄö',
					'poor': 'Â∑Æ'
				}
				return levelMap[level] || 'Êú™Áü•'
			},
			
			// Ëé∑ÂèñÂÆåÊàêÁ≠âÁ∫ß
			getCompletionLevel(rate) {
				if (rate >= 100) return '‰ºòÁßÄ'
				if (rate >= 80) return 'ËâØÂ•Ω'
				if (rate >= 60) return '‰∏ÄËà¨'
				if (rate >= 40) return 'ÈúÄÂÖ≥Ê≥®'
				return 'ÈúÄÂπ≤È¢Ñ'
			},
			
			// Ëé∑ÂèñÂÆåÊàêÊèèËø∞
			getCompletionDescription(rate) {
				if (rate >= 100) return 'ÂêÑÈ°πÊåáÊ†áÂÆåÂÖ®ËææÊ†á'
				if (rate >= 80) return 'Â§ßÈÉ®ÂàÜÊåáÊ†áËææÊ†áÔºåÂèëËÇ≤ËâØÂ•Ω'
				if (rate >= 60) return 'Âü∫Êú¨ËææÊ†áÔºåÈÉ®ÂàÜÊñπÈù¢ÈúÄÂä†Âº∫'
				if (rate >= 40) return 'ÈÉ®ÂàÜËææÊ†áÔºåÂª∫ËÆÆÈáçÁÇπÂÖ≥Ê≥®'
				return 'ËææÊ†áÁéáËæÉ‰ΩéÔºåÂª∫ËÆÆÂèäÊó∂Âπ≤È¢Ñ'
			},
			
			// ÁîüÊàê‰∏ì‰∏öÂª∫ËÆÆ
			generateRecommendations() {
				const recommendations = []
				
				// ÂàÜÊûêÂêÑÈ¢ÜÂüüË°®Áé∞
				const poorDomains = []
				const fairDomains = []
				const goodDomains = []
				const normalDomains = []
				
				Object.keys(this.domainStats).forEach(domain => {
					const stats = this.domainStats[domain]
					if (stats.level === 'poor') poorDomains.push(domain)
					else if (stats.level === 'fair') fairDomains.push(domain)
					else if (stats.level === 'good') goodDomains.push(domain)
					else if (stats.level === 'normal') normalDomains.push(domain)
				})
				
				// Ê†πÊçÆÊÄª‰ΩìË°®Áé∞ÁªôÂá∫Âª∫ËÆÆ
				if (this.overallCompletionRate >= 80) {
					recommendations.push('Â≠©Â≠êÂèëËÇ≤Áä∂ÂÜµËâØÂ•ΩÔºåÂêÑÈ°πÊåáÊ†áËææÂà∞Áõ∏Â∫îÂπ¥ÈæÑÊÆµÊ≠£Â∏∏Ê∞¥Âπ≥')
					recommendations.push('Âª∫ËÆÆÁªßÁª≠‰øùÊåÅËâØÂ•ΩÁöÑÂÖªËÇ≤ÁéØÂ¢ÉÂíå‰∫íÂä®ÊñπÂºè')
					recommendations.push('ÂèØ‰ª•ÈÄÇÂΩìÂ¢ûÂä†‰∏Ä‰∫õÊåëÊàòÊÄßÊ¥ªÂä®Ôºå‰øÉËøõËøõ‰∏ÄÊ≠•ÂèëÂ±ï')
				} else if (this.overallCompletionRate >= 60) {
					recommendations.push('Â≠©Â≠êÂèëËÇ≤Âü∫Êú¨Ê≠£Â∏∏ÔºåÈÉ®ÂàÜÊñπÈù¢ËøòÊúâÊèêÂçáÁ©∫Èó¥')
					recommendations.push('Âª∫ËÆÆÈáçÁÇπÂÖ≥Ê≥®ÂæóÂàÜËæÉ‰ΩéÁöÑÂèëËÇ≤Áª¥Â∫¶')
					recommendations.push('ÂèØ‰ª•ÂØªÊ±Ç‰∏ì‰∏öÂ∫∑Â§çÂ∏àÁöÑÊåáÂØºÂíåÂª∫ËÆÆ')
				} else {
					recommendations.push('Âª∫ËÆÆÂèäÊó∂Âí®ËØ¢‰∏ì‰∏öÂåªÁîüÊàñÂ∫∑Â§çÂ∏à')
					recommendations.push('ËøõË°åÊõ¥ËØ¶ÁªÜÁöÑÂèëËÇ≤ËØÑ‰º∞ÂíåËØäÊñ≠')
					recommendations.push('Âà∂ÂÆö‰∏™ÊÄßÂåñÁöÑÊó©ÊúüÂπ≤È¢ÑËÆ°Âàí')
				}
				
				// ÈíàÂØπÂÖ∑‰ΩìÈ¢ÜÂüüÁªôÂá∫Âª∫ËÆÆ
				if (poorDomains.length > 0) {
					recommendations.push(`${poorDomains.join('„ÄÅ')}ÊñπÈù¢ÈúÄË¶ÅÈáçÁÇπÂÖ≥Ê≥®ÂíåËÆ≠ÁªÉ`)
				}
				if (fairDomains.length > 0) {
					recommendations.push(`${fairDomains.join('„ÄÅ')}ÊñπÈù¢ÊúâÊèêÂçáÁ©∫Èó¥ÔºåÂª∫ËÆÆÂä†Âº∫ÁªÉ‰π†`)
				}
				if (goodDomains.length > 0) {
					recommendations.push(`${goodDomains.join('„ÄÅ')}ÊñπÈù¢Ë°®Áé∞ËâØÂ•ΩÔºåÂèØ‰ª•ÁªßÁª≠‰øùÊåÅ`)
				}
				if (normalDomains.length > 0) {
					recommendations.push(`${normalDomains.join('„ÄÅ')}ÊñπÈù¢ÂÆåÂÖ®ËææÊ†áÔºåÊòØÂ≠©Â≠êÁöÑ‰ºòÂäøÈ¢ÜÂüü`)
				}
				
				// Ê∑ªÂä†ÈÄöÁî®Âª∫ËÆÆ
				recommendations.push('ÂÆöÊúüËøõË°åÂèëËÇ≤ËØÑ‰º∞ÔºåËøΩË∏™ËøõÊ≠•ÊÉÖÂÜµ')
				recommendations.push('‰øùÊåÅËÄêÂøÉÂíåÁßØÊûÅÁöÑÊÄÅÂ∫¶ÔºåÊØè‰∏™Â≠©Â≠êÈÉΩÊúâËá™Â∑±ÁöÑÂèëËÇ≤ËäÇÂ•è')
				
				this.recommendations = recommendations
			},
			
			// Êü•ÁúãÂéÜÂè≤ËÆ∞ÂΩï
			viewHistory() {
				uni.navigateTo({
					url: '/pages/history/history'
				})
			},
			
			// ÈáçÊñ∞ËØÑ‰º∞
			newAssessment() {
				uni.showModal({
					title: 'ÈáçÊñ∞ËØÑ‰º∞',
					content: 'Á°ÆÂÆöË¶ÅÂºÄÂßãÊñ∞ÁöÑËØÑ‰º∞ÂêóÔºüÂΩìÂâçËØÑ‰º∞ÁªìÊûúÂ∞ÜË¢´Ë¶ÜÁõñ„ÄÇ',
					success: (res) => {
						if (res.confirm) {
							// Ê∏ÖÈô§ÂΩìÂâçËØÑ‰º∞Êï∞ÊçÆ
							uni.removeStorageSync('assessmentForm')
							uni.removeStorageSync('assessmentResult')
							
							// Ë∑≥ËΩ¨Âà∞ÂÑøÁ´•‰ø°ÊÅØÈ°µ
							uni.redirectTo({
								url: '/pages/child-info/child-info'
							})
						}
					}
				})
			}
		}
	}
</script>

